import avatar from "../../components/yq-avatar/yq-avatar.vue"
import {AddCheater,UploadCheaterIcon} from '@/api/modules/cheater.js'

export default {
	components:{
		avatar,
	},
	data() {
		return {
			data: {
				caseId: "",
				name: "",
			    approximateAge: 0,
				accentOrBirthplace: "",
				phone: "",
				weChat: "",
				eMail: "",
				otherAppId: "",
				otherAppName: "",
				bankCard: "",
				photoUrl: "",
				photoThumbUrl: "",
			},
			percent: 0,
			url: "../../static/missing-face.png",
		}
	},
	onLoad(options) {
		if(options && options.sceneId){
			this.data.caseId = options.sceneId
		}
	},
	
	
	methods: {
		
		/**
		 * 添加骗子
		 */
		addCheater(){
			uni.showLoading({
			    title: '提交中...',
			    mask: true // 默认遮罩出现可以继续操作
			});
			const _this = this;
			AddCheater(this.data).then(res=>{
				uni.hideLoading()
				if(res){
					_this.$eventBus.$emit('addCheaterSuccess');
					uni.navigateBack();
				}
			}).catch(err=>{
				uni.hideLoading()
				console.log('添加骗子失败！')
			});
			
		},
		
		
		/**
		 * 上传骗子头像
		 */
		uploadIcon(rsp){
			const _this = this;
			this.url = rsp.path; //更新头像方式一
			
			let uploadData = {
				filePath: _this.url
			}
			//上传图片
			UploadCheaterIcon(uploadData).then(res=>{
				if(res && res.data){
					var resList = JSON.parse(res.data);
					if(resList.length>1){
						_this.data.photoUrl = resList[0].filePath
						_this.data.photoThumbUrl = resList[0].filePath
					}else{
						console.log('上传骗子头像返回为空！');
					}
				}
			}).catch(err=>{
				console.log('上传骗子头像失败！')
			})
			
			
		},
		
	}
}
